import React from 'react';
import { useParams, Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Card, Row, Col } from 'antd';
import { RootState } from '../store/rootReducer';
import CatDetail from '../components/CatDetail';
import styles from './CatDetailsPage.module.css';

const { Meta } = Card;

const CatDetailsPage: React.FC = () => {
    const { catId } = useParams<{ catId: string }>();
    const cat = useSelector((state: RootState) =>
        state.cats.cats.find((cat) => cat.id === catId)
    );

    const cats = useSelector((state: RootState) => state.cats.cats);

    if (!cat) {
        return <div>  .</div>;
    }

    const sortedCats = [...cats].sort((a, b) => {
        if (a.id === catId) return -1;
        if (b.id === catId) return 1;
        return 0;
    });

    return (
        <Row gutter={[16, 16]}>
            <Col xs={24} sm={24} md={12} lg={16} xl={18}>
                <CatDetail cat={cat} />
            </Col>
            <Col xs={24} sm={24} md={12} lg={8} xl={6}>
                <div className={styles.catList}>
                    <Row gutter={[16, 16]}>
                        {sortedCats.map((cat) => (
                            <Col key={cat.id} xs={24}>
                                <Link to={`/cat/${cat.id}`}>
                                    <Card
                                        cover={<img alt={cat.name} src={cat.imageUrl} />}
                                        className={cat.id === catId ? styles.selected : ""}
                                        style={{ maxWidth: 300 }}
                                    >
                                        <Meta title={cat.name} description={cat.breed} />
                                    </Card>
                                </Link>
                            </Col>
                        ))}
                    </Row>
                </div>
            </Col>
        </Row>
    );
};

export default CatDetailsPage;
