import React from 'react';
import { Cat } from '../store/cats/catsSlice';
import { Descriptions, Image, Tag } from 'antd';
import {Link} from "react-router-dom";

interface CatDetailProps {
    cat: Cat;
}

const CatDetail: React.FC<CatDetailProps> = ({ cat }) => {
    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '1rem' }}>
                {cat.tags && cat.tags.map((tag) => (
                    <Tag key={tag} color="purple">
                        {tag}
                    </Tag>
                ))}
            </div>
            <Image
                src={cat.imageUrl}
                alt={cat.name}
                preview={false}
                style={{
                    minWidth: '300px',
                    minHeight: '300px',
                    maxWidth: '350px',
                    maxHeight: '300px',
                    borderRadius: '20px',
                    marginBottom: '1rem',
                    display: 'block',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    objectFit: "cover",

                }}
            />
            <Descriptions
                bordered
                column={{ xs: 1, sm: 1, md: 1, lg: 2, xl: 2, xxl: 2 }}
                style={{ marginBottom: '1rem', fontSize: '14px' }}
            >
                <Descriptions.Item label="">{cat.age}</Descriptions.Item>
                <Descriptions.Item label="">{cat.breed}</Descriptions.Item>
                <Descriptions.Item label="">{cat.description}</Descriptions.Item>
            </Descriptions>
            <Link to={`/contact`} style={{color: "black"}}>     , '  !</Link>
        </div>
    );
};

export default CatDetail;
