import React, {useState} from 'react';
import {Card as AntCard, Button} from 'antd';
import CatModal from './CatModal';
import {Cat} from '../store/cats/catsSlice';

interface Props {
    cat: Cat;
}

const Card: React.FC<Props> = ({cat}) => {
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const closeModal = () => {
        setIsModalVisible(false);
    };

    return (
        <>
            <AntCard
                hoverable
                style={{width: 240}}
                cover={<img alt={cat.name} src={cat.imageUrl} style={{maxHeight: 154, objectFit: "cover"}}/>}
                onClick={showModal}
            >
                <AntCard.Meta title={cat.name}/>
            </AntCard>
            {isModalVisible && <CatModal cat={cat} onClose={closeModal}/>}
        </>
    );
};

export default Card;
